import React from 'react';
import { EmailButton } from 'components/atoms';
import { IOrderList } from './OrderList';
import { IDiscounts } from './DiscountCheckboxes';

interface Props {
  orders: IOrderList,
  discounts: IDiscounts;
  cost: string;
  disabled?: boolean;
}

const endl = '%0D%0A' as const;

const OrderEmailButton = ({orders, discounts, cost, disabled}: Props): JSX.Element => {
  const formOrderReceipt = () => {
    return Object.entries(orders).map(
      ([cardType, {amount}]) => {
        if (amount > 0)
          return ('  - ' + cardType + ': ' + amount + endl);
        else
          return '';
      }
    ).join('');
  };

  const formDiscountReceipt = () => {
    const hasDiscountSelected = Object.values(discounts)
      .map(({amount, active}) => active).some((bool) => bool === true);
    if (hasDiscountSelected)
      return "Discounts:" + endl + Object.entries(discounts).map(
        ([discountType, {amount, active}]) => {
          if (active)
            return ('  - ' + discountType + endl);
          else
            return '';
        }
      ).join('');
    else
        return '';
  };

  return (
    <EmailButton
      email='kayladuda123@gmail.com'
      subject='Card Order'
      body={
        "Order:" + endl +
        formOrderReceipt() + endl +
        formDiscountReceipt() + endl +
        "Total cost: " + cost + endl
      }
      disabled={disabled}
    />
  );
};

export default OrderEmailButton;

