import { Container } from '@mui/material';
import React, { CSSProperties, PropsWithChildren } from 'react';
import Color from 'ts-color-class';

interface PaneProps extends PropsWithChildren<any> {
	color: Color;
}

const styles: Record<string, CSSProperties> = {
  Pane: {
    marginTop: 20,
    paddingTop: 20,
    paddingBottom: 1,
    paddingLeft: 8,
    borderStyle: 'solid',
    borderWidth: 3,
    borderRadius: 5,
  }
};


const Pane = ({ children, color }: PaneProps): JSX.Element => {
  return (
    <Container 
      maxWidth="lg" 
      className="Pane" 
      style={{
        ...styles.Pane, 
        background: color.getHex(), 
        borderColor: color.darken(0.2).getHex()
      }}
    >
        { children }
    </Container>
  );
}

export default Pane;

