import React, { CSSProperties } from 'react';
import { Carousel } from 'react-bootstrap';
import { cards, ICard } from '../../assets';


const styles: Record<string, CSSProperties> = {
    Image: {
        maxWidth: '60%',
        maxHeight: '60%',
        margin: 'auto',
        display: 'block',
    },
    paragraph: {
        // fontSize: 14,
    }
};

interface Props {
    descriptive?: boolean;
}

const CardCarousel = ({ descriptive }: Props): JSX.Element => (
    <Carousel>
        {
            cards.map((card, index) => 
                <Carousel.Item key={index} interval={5000}>
                    <img
                        className="d-block w-100"
                        style={styles.Image} 
                        src={card.image}
                        alt="First slide"
                    />
                    {
                        descriptive && (
                            <Carousel.Caption>
                                <h3>{card.title}</h3>
                                <p style={styles.paragraph}>{card.details}</p>
                            </Carousel.Caption>
                        )
                    }
                </Carousel.Item>
            )
        }
    </Carousel>
);


export default CardCarousel;