

import React, { CSSProperties } from 'react';
import { orangeCopperfield } from 'styles';
import { Checkbox } from '@material-ui/core';


const styles: Record<string, CSSProperties> = {
  ul: {
    listStyleType: 'none',
    margin: 0,
    padding: 0,
  },
  Checkbox: {
    color: orangeCopperfield.lighten(0.3).getHex(),
  },
};

export type DISCOUNT_TYPE = "Family Members" | "Mental Health Diagnosis" | "Military Members";
export type IDiscounts = Record<DISCOUNT_TYPE, { amount: number, active: boolean }>;

export const INITIAL_DISCOUNTS: IDiscounts = {
  "Family Members": {
    amount: 5, // percent
    active: false,
  },
  "Mental Health Diagnosis": {
    amount: 5,
    active: false,
  },
  "Military Members": {
    amount: 5,
    active: false,
  },
};

interface Props {
  discounts: IDiscounts;
  setDiscounts: (discount: IDiscounts) => void; 
}

const DiscountCheckboxes = ({ discounts, setDiscounts }: Props): JSX.Element => {
  const toggleDiscount = (type: DISCOUNT_TYPE, active: boolean) => {
    setDiscounts({...discounts, [type]: {...discounts[type], active: active}});
  };
  return (
    <ul style={styles.ul}>
      {
        Object.entries(discounts).map(([discount, {amount, active}], index) => {
          const discountType = discount as DISCOUNT_TYPE;
          return (
            <li key={index}>
              <Checkbox 
                style={styles.Checkbox}
                checked={active}
                onChange={() => toggleDiscount(discountType, !active)}
              />
                {amount}% off for {discountType} 
            </li>
          );
        })
      }
    </ul>
  );
};

export default DiscountCheckboxes;

