import { Button, Container, Grid, TextField, Typography } from '@mui/material';
import React, { CSSProperties } from 'react';
import { blue, gray, lime, orangeCopperfield, red } from 'styles';


const styles: Record<string, CSSProperties> = {
    ul: {
      listStyleType: 'none',
      margin: 0,
      padding: 0,
    },
    TextField: {},
    BrightTextField: {
        backgroundColor: gray.lighten(0.15).getHex(),
    },
    DecrementButton: {
        backgroundColor: orangeCopperfield.getHex(),
    },
    IncrementButton: {
        backgroundColor: blue.getHex(),
    },
  };

export type CARD_TYPE = "Anniversary" | "Wedding" | "Birthday" | "Sympathy" | "Get Well Soon" | "Just Because" | "Thank you" | "You're the best!" | "Celebration";
export type IOrderList = Record<CARD_TYPE, { amount: number, cost: number }>;

export const INITIAL_ORDER_LIST: IOrderList = {
    "Anniversary": {
        amount: 0,
        cost: 5,
    },
    "Wedding": {
        amount: 0,
        cost: 5,
    },
    "Birthday": {
        amount: 0,
        cost: 5,
    },
    "Sympathy": {
        amount: 0,
        cost: 5,
    },
    "Get Well Soon": {
        amount: 0,
        cost: 5,
    },
    "Just Because": {
        amount: 0,
        cost: 5,
    },
    "Thank you": {
        amount: 0,
        cost: 5,
    },
    "You're the best!": {
        amount: 0,
        cost: 5,
    },
    "Celebration": {
        amount: 0,
        cost: 5,
    },
};

interface Props {
    orders: IOrderList;
    setOrders: (orders: IOrderList) => void;
}

const OrderList = ({ orders, setOrders }: Props): JSX.Element => {
    const setOrderAmount = (type: CARD_TYPE) => (amount: number) => {
        setOrders({...orders, [type]: {...orders[type], amount: amount}});
    }
    return (
        <ul style={styles.ul}>
            {
                Object.keys(orders).map((orderType, index) => 
                {
                    const cardType = orderType as CARD_TYPE;
                    return (
                        <OrderItem 
                            key={index}
                            type={cardType}
                            amount={orders[cardType].amount}
                            cost={orders[cardType].cost}
                            setOrderAmount={setOrderAmount(cardType)}
                        />
                    )
                })
            }
        </ul>
    );
};

interface OrderItemProps {
    type: CARD_TYPE;
    amount: number;
    cost: number;
    setOrderAmount: (amount: number) => void;
}

const OrderItem = ({ type, amount, setOrderAmount }: OrderItemProps): JSX.Element => (
    <li>
        <Container
            maxWidth='sm'
        >
            <Grid container spacing={2}>
                <Grid item xs={3}>
                    <Typography
                        component="p"
                        variant="body1"
                        style={{display: 'inline-block'}}
                        fontFamily="Josefin Sans"
                    >
                        {type}
                    </Typography>
                </Grid>
                <Grid item xs={9}>
                    <>
                        <Button 
                            variant='contained'
                            style={styles.DecrementButton}
                            onClick={() => {
                                if (isNaN(amount))
                                    setOrderAmount(0);
                                if (amount - 1 >= 0)
                                    setOrderAmount(amount - 1);
                            }}
                        >
                            <strong> - </strong> 
                        </Button>
                        <TextField 
                            id={type + "_textbox"}
                            variant='filled'
                            type="number"
                            size='small'
                            style={amount > 0 ? styles.BrightTextField : styles.TextField}
                            value={amount}
                            onChange={(event) => {
                                if (!event.target.value)
                                    setOrderAmount(NaN);
                                else if (parseInt(event.target.value) >= 0)
                                    setOrderAmount(parseInt(event.target.value));
                                else
                                    setOrderAmount(0);
                            }}
                        />
                        <Button 
                            variant='contained'
                            style={styles.IncrementButton} 
                            onClick={() => {
                                if (isNaN(amount))
                                    setOrderAmount(1);
                                else
                                    setOrderAmount(amount + 1);
                            }}
                        >
                            <strong> + </strong> 
                        </Button>
                    </>
                </Grid>
            </Grid>
        </Container>
    </li>
);

export default OrderList;