import React, { CSSProperties, useMemo, useState } from 'react';
import { createTheme, CssBaseline, ThemeProvider, Typography } from '@mui/material';
import { Pane, Main } from 'components/atoms';
import { VenmoButton, CardCarousel, OrderList, DiscountCheckboxes, OrderEmailButton } from 'components/molecules';
import { blue, darkGray, gray, orangeCopperfield, purple } from 'styles';
import { INITIAL_ORDER_LIST, IOrderList } from './molecules/OrderList';
import { toMoneyFormat } from 'utils'; 
import { IDiscounts, INITIAL_DISCOUNTS } from './molecules/DiscountCheckboxes';

const theme = createTheme({
  palette: {
    mode: 'dark',
  },
});

const styles: Record<string, CSSProperties> = {
  ul: {
    listStyleType: 'none',
    margin: 0,
    padding: 0,
  }
};

export const CARD_COST = 5 as const;

const App = (): JSX.Element => {
  const [discounts, setDiscounts] = useState<IDiscounts>(INITIAL_DISCOUNTS);
  const [orders, setOrders] = useState<IOrderList>(INITIAL_ORDER_LIST);

  const cost = useMemo(() => {
    const totalCardCost = Object.values(orders).reduce((totalCardCost, {amount, cost}) => 
      totalCardCost + amount * cost, 0);
    const totalDiscount = Object.values(discounts).reduce((totalDiscount, {amount, active}) => 
      active ? totalDiscount + amount : totalDiscount, 0);
    return totalCardCost - (totalCardCost * (totalDiscount / 100.0));
  }, [discounts, orders]);

  const hasNoOrdersSelected = useMemo(() => {
    const numCardsOrdered = Object.values(orders).reduce((totalAmount, {amount}) => 
      totalAmount + amount, 0);
    return numCardsOrdered === 0;
  }, [orders]);
  
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Main>
        <Pane color={purple}>
          <Typography
            component="h1"
            variant="h2"
            align="center"
            fontFamily="Josefin Sans"
            color="white"
            gutterBottom
          >
            Cards by Kayla
          </Typography>
        </Pane>
        <Pane color={blue}>
          <Typography
            component="h1"
            variant="h6"
            align="center"
            fontFamily="Josefin Sans"
            color="white"
            gutterBottom
          >
            Thank you for supporting my new business, as every card is filled with love. 
            Each handmade card is {toMoneyFormat(CARD_COST)} (including an envelope) and are blank inside. 
            Please see below for the following <strong>stackable</strong> discounts. ❤️
          </Typography>
        </Pane>
        <Pane color={gray}>
          <CardCarousel />
        </Pane>
        <Pane color={orangeCopperfield}>
          <Typography
            component="h4"
            variant="h4"
            align="center"
            fontFamily="Josefin Sans"
            color="white"
            gutterBottom
          >
            Discounts:
          </Typography>
          <Typography
            component="h1"
            variant="h6"
            align="center"
            fontFamily="Josefin Sans"
            color="white"
            gutterBottom
          >
            <DiscountCheckboxes 
              discounts={discounts}
              setDiscounts={setDiscounts}
            />
          </Typography>
        </Pane>
        <Pane color={blue}>
          <Typography
            component="h4"
            variant="h4"
            align="center"
            fontFamily="Josefin Sans"
            color="white"
            gutterBottom
          >
            Special Offers:
          </Typography>
          <Typography
            component="h1"
            variant="h6"
            align="center"
            fontFamily="Josefin Sans"
            color="white"
            gutterBottom
          >
            <ul style={styles.ul}>
              <li>
                - Buy 15 cards or more and receive a free carrying case to organize your cards (limit 1 per person)
              </li>
              <li>
                - Receive one free Thank you card after purchase (limit 1 per person)
              </li>
            </ul>
          </Typography>
        </Pane>
        <Pane color={gray}>
          <OrderList 
            orders={orders}
            setOrders={setOrders}
          />
          <Typography
            component="h6"
            variant="h6"
            align="center"
            fontFamily="Josefin Sans"
            color="white"
            gutterBottom
            style={{ paddingTop: 30}}
          >
            The amount due is {toMoneyFormat(cost)}
          </Typography>
        </Pane>
        <Pane color={darkGray}>
          <Typography
            component="h5"
            variant="h5"
            align="center"
            fontFamily="Josefin Sans"
            color="white"
            gutterBottom
            style={{ paddingTop: 30}}
          >
            Payment instructions
          </Typography>
          
          <ul style={styles.ul}>
            <li>
              <Typography
                component="h6"
                variant="h6"
                align="center"
                fontFamily="Josefin Sans"
                color="white"
                gutterBottom
              >
                1. Send the email
              </Typography>
            </li>
            <li style={{ textAlign: 'center', marginBottom: 20 }}>
              <OrderEmailButton
                orders={orders}
                discounts={discounts}
                cost={toMoneyFormat(cost)}
                disabled={hasNoOrdersSelected}
              />
            </li>
            <li>
              <Typography
                component="h6"
                variant="h6"
                align="center"
                fontFamily="Josefin Sans"
                color="white"
                gutterBottom
              >
                2. Pay via Venmo:
              </Typography>
            </li>
            <li style={{ textAlign: 'center', marginBottom: 20 }}>
              <VenmoButton 
                disabled={hasNoOrdersSelected}
              />
            </li>
          </ul>
        </Pane>
      </Main>
    </ThemeProvider>
  );
}

export default App;
